.header__logo {
  border-bottom: 1px solid var(--black100);

  @media (max-width: 800px) {
    border-bottom: 0;
  }
  a {
    display: flex;
    padding: 10px;
    margin: 10px;

    em {
      width: 40px;
      height: 40px;
      display: block;
      background-color: var(--red);
      margin-right: 10px;
      animation: ani 20s 20s ease-in-out infinite;
    }

    span {
      font-size: 18px;
      font-weight: 900;
      line-height: 1;
      text-transform: uppercase;
    }
  }
}

.header__menu {
  padding: 10px 0;

  .menu {
    border-bottom: 1px solid var(--black100);

    li {
      margin-bottom: 5px;

      a {
        position: relative;
        font-size: 1rem;
        display: block;
        padding: 0.9rem 2rem 0.9rem 3.125rem;
        margin: 0 0.625rem;
        border-radius: 40px;
        line-height: 1;
        color: var(--white200);
        transition: all 0.3s;

        svg {
          width: 1.25rem;
          height: 1.25rem;
          position: absolute;
          left: 1.25rem;
          top: 0.75rem;
        }

        &:hover {
          background-color: var(--black100);
          color: var(--white);
        }
      }

      &.active a {
        background-color: var(--black100);
        color: var(--white);
      }
    }
  }

  .keyword {
    padding: 1.25rem;

    li {
      display: inline-block;

      a {
        display: inline-block;
        padding: 5px 15px;
        margin: 3px 1px;
        border: 1px solid var(--black200);
        font-size: 0.8rem;
        border-radius: 40px;
        transition: all 0.3s;

        &:hover {
          background-color: var(--black100);
          color: var(--white);
        }
      }

      &.active a {
        background-color: var(--black100);
        color: var(--white);
      }
    }
  }
}

.header__sns {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 260px;
  text-align: center;
  padding: 15px 0 10px;
  background-color: var(--blackAlpha100);
  backdrop-filter: blur(10px);

  @media (max-width: 800px) {
    display: none;
  }

  li {
    display: inline-block;

    a {
      padding: 5px 7px;
      display: inline-block;
    }
  }
}

@keyframes ani {
  0% {
    transform: rotate(0) scale(1);
    border-radius: 0;
  }
  50% {
    transform: rotate(720deg) scale(0.5);
    border-radius: 50px;
  }
  100% {
    transform: rotate(0) scale(1);
    border-radius: 0;
  }
}
