.today__inner {
  display: flex;
  background-color: var(--black100);
  margin: 0 0 3.125rem 0;
}

.today__thumb {
  width: 40%;
}

.today__text {
  width: 60%;
  padding: 2rem;

  .today {
    background-color: #f00;
    padding: 2px 50px;
    border-radius: 20px;
    text-transform: uppercase;
    margin: 0 0 10px 0;
    display: inline-block;
  }

  .title {
    font-size: 2rem;
    margin: 0 0 1rem 0;
    @include line-one;
  }

  .desc {
    margin: 0 0 1rem 0;
    color: var(--white200);
    @include line(4);
  }

  .info {
    .author {
      font-size: 1rem;
      margin: 0 10px 0 0;
      font-weight: 900;
      text-decoration: underline;
      text-underline-position: under;
      text-transform: uppercase;
    }
    .data {
      font-style: 0.8rem;
      color: var(--white200);
    }
  }
}

@media (max-width: 1700px) {
  .today__inner {
    .today__thumb {
      width: 500px;
    }

    .today__text {
      width: calc(100% - 500px);

      .desc {
        @include line(2);
      }
    }
  }
}

@media (max-width: 1200px) {
  .today__inner {
    flex-direction: column;

    .today__thumb {
      width: 100%;
    }

    .today__text {
      width: 100%;

      .title {
        font-size: 24px;
      }

      .desc {
        display: none;
      }
    }
  }
}

@media (max-width: 800px) {
  #todayPage,
  #today {
    margin: 80px 0 0 0;
  }
}
